import React from "react";
import { navigate } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";
import useEditionData from "../../hooks/useEditionData";

type RegisterButtonProps = {
  className?: string;
  inNavbar?: boolean;
};

const RegisterButton: React.FC<RegisterButtonProps> = ({
  className,
  inNavbar,
}) => {
  const { t } = useTranslation();
  const currentEdition = useEditionData(2024);
  const registrationIntervals = currentEdition.hackathonRegistration.split("-");
  const registrationDates = [
    new Date(`${registrationIntervals[0]} 2024`),
    new Date(`${registrationIntervals[1]} 2024`),
  ];
  const today = new Date();
  const registrationLink =
    "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroHackathon/formperma/pHkcA9Uoak67yDFN8favZvt4aN1PTQSw1mkKYIsnATc";
  const registerRedirect = () => {
    if (today < registrationDates[0]) navigate("/schedule");
    else if (today < registrationDates[1])
      window.open(registrationLink, "_blank", "noreferrer");
    else window.open(currentEdition.leaderboard, "_blank", "noreferrer");
  };
  const registerText = () => {
    if (today < registrationDates[0]) return t("navbar.schedule");
    else if (today < registrationDates[1]) return t("register");
    return t("navbar.leaderboard");
  };

  return (
    !(today < registrationDates[0] && inNavbar) && (
      <Button className={` ${className}`} onClick={() => registerRedirect()}>
        {registerText()}
      </Button>
    )
  );
};

export default RegisterButton;
